.header-menu > li > a {
  padding: 33px 12px;
  display: block;
}
.header-menu > li > a:after {
  color: transparent;
  content: attr(data-title);
  display: block;
  font-weight: 500;
  height: 1px;
  margin-bottom: -1px;
  overflow: hidden;
  visibility: hidden;
}
.header-menu > li > a:before {
