  z-index: 10;
  top: 100%;
  left: 0;
  margin-top: -1px;
  height: 2px;
  background: #e13d55;
  width: 100%;
  transition: all 0.3s ease-out;
  opacity: 0;
}
.header-menu > li:hover > a {
